body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html{
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  background: #131F2E;
  user-select: none;
  font-family: lato, sans-serif;
  color: #eee;
  transition: 0.7s ease;
}

App{
  width: 100%;
  overflow: hidden;
  height: 100%;
}

/*
ROM SPECIAL
 */
.rom-app {
  overflow: auto;
  height: 100vh;
}

:root{
  --primary: #071322;
  --secondary: rgba(11, 17, 45, 0.7);
  --title: #fff;
}



.enucs{
  font-size: 67px;
  margin-top: 10px;
  font-weight: bold;
  text-shadow: 1px 2px 12px rgba(0, 0, 0, 0.38);
}

.text-shadow{
  text-shadow: 1px 2px 6px rgba(0, 0, 0, 0.38);
}

.circle{
  margin: 10px;
  width: 24px;
  height: 24px;
  border-radius: 12px;

}

.back-red{
  background-color: #d24949;
}
.back-pink{
  background-color: #c55c5c;
}
.back-white{
  background-color: #eeeeee;
}


.right-container{
  width: 60%;
  background-color: #1c4254;
  right: 0px;
  height: calc(100vh - 50px);
}

.home-cour{
  background-image: url('./imgs/code.jpg');
  filter: blur(2.6px);
  z-index: -1;
  opacity: 0.56;
  background-repeat: no-repeat;
  background-size: cover;
  width: 60%; position: absolute;
  text-align: center;
  height: calc(100vh - 50px);
  box-shadow: 1px 2px 2px 2px rgba(19, 18, 18, 0.7);
}

.home-1{display:flex; flex:1; height:calc( 100vh - 50px); width:100%; position:relative}

.text {
  text-align: center;
  z-index: 12;
}

.full{
  width: 100%;
  height: 100%;
}

.page {
  width: 100%;
  height: 100%;
  flex: 1;
  margin-bottom: 0px;
  margin-top:50px;
  bottom: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

/* layout styles */
nav{
  background:  var(--primary) ;
  background-blend-mode: screen;
  width: 100%;
  height: 50px;
  align-items: center;
  display: flex;
  vertical-align: center;
  align-content: center;
  backdrop-filter: blur(1.2px);
  padding-bottom: 10px;
  position: fixed;
  position: fixed;
  z-index: 100;
  top: 0; /* required */
}
nav a{
  vertical-align: center;
  color: var(--title);
}
nav .sidenav-trigger{
  margin: 0;
}

a:link{
  font-family: lato, sans-serif;
  text-underline: none;
  text-decoration: none;
}

a.items {
  margin: 12px;
  vertical-align: middle;
  font-size: 19px;
}

span.red {
  color: #d24949;
}
/* recipe styles */
.recipes{
  margin-top: 9px;
  margin-bottom: 20px;
  padding: 30px;
  padding-top: 10px;
  transition: 0.4s ease;
  transform: translateY(90%);
  position: relative;
}
.card-panel, .recipe{
  width: 100%;
  margin-top: 20px;
  margin-bottom 	: 10px;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 1px 2px 11px 2px rgba(90, 90, 90, 0.074);
  display: grid;
  grid-template-columns: 2fr 6fr 1fr;
  grid-template-areas: "image details delete";
  position: relative;
}
.recipe img{
  grid-area: image;
  max-width: 60px;
}
.recipe-details{
  grid-area: details;
  color:#b2b2b2;
  margin-top: 6px;
  margin-left: 4px;
}
.recipe-delete{
  grid-area: delete;
  position: absolute;
  bottom: 0px;
  right: 0px;
}
.recipe-delete i{
  font-size: 18px;
}
.recipe-title{
  margin: 4px;
  font-size: 18px;
  color: #8e8e8e;
}
.recipe-ingredients{
  font-size: 0.8em;
}
.left{
  align-self:flex-start;
}
br{
  margin-top: 0%;
}

/* form-styles */
.add-btn{
  background: var(--primary) !important;
}

input{
  font-family: lato, sans-serif;
  width: 80%;
  font-size: 14px;
  box-shadow: none !important;
  border-bottom: 1px solid red;
  -webkit-box-shadow: none !important;
}

input.noner{
  border-bottom: 1px solid rgba(49, 49, 49, 0.079);
}

form .input-field-101{
  border-width: 1em;
  border-radius: 11px;
  background-color: #eee;
  border-color: #a4a4a4;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 10px;

}

input.PPTX {
  border-width: 0em;
  border-radius: 11px;
  background-color: #eee;
  border-color: #a4a4a400;
  margin-top: 10;
  margin-bottom: 10;
  margin-right: 10px;

}

.side-form button{
  border-radius: 7px;
  background: var(--primary);
  box-shadow: 1px 1px 3px rgba(90,90,90,0.2);
}
form .input-field{
  margin-top: 30px;
}

