.ele{

  outline:none;
  border-width: 0px;
  background: inherit;
  color: #43abc5;
  height: 100%;
  min-height: 48px;
  z-index: inherit;
  position: relative;
  width:32%;
  margin: 0px;
  margin-left: 0;
  margin-right: 0;
  padding: 0;
  right:0;
  left:0;

}

.ele:hover{
  transition: 0.2s;
  background: #3d3d3d;
  color:#fffffb;
}

.ele:focus{
  transition: 0.2s;
  background: #fefefe;
  color:#3d3d3d;
}


