@media screen and (max-width: 900px) {

  .mobile-nav{
    width: 100%;
    height: 70px;
    opacity: 1;
    visibility: visible;
    backdrop-filter: blur(1.3px);
    -webkit-backdrop-filter: blur(1.3px);
    box-shadow: 4px 1px 9px rgba(110, 109, 109, 0.27);
  }

  .recipes{
    margin-bottom: 80px;
  }

  .sidenav-side-form{
    transition: 0.3s ease-out;
    width:70%;
    visibility: visible;
  }
  .panel-left{

    width: 0;
    opacity: 0;
    float: left;
    visibility: hidden;
    transition: 0.3s ease;
    top:0;
    left: 0;
    height: 100%;
    background-color: #f0f7ff;
  }

  .panel-right{
    opacity: 0;
    width: 0;
    float: left;
    visibility: hidden;
    transition: 0.3s ease;
    height: 100%;
    right: 0;
    top:0%;
    background-color: #ffffff;
  }

  .hider {
    opacity: 0;
    transition: 0.3s ease;
    visibility: hidden;
  }

  .flex {
    display:inline;
  }

  .panelList {
    width:100%;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 0%;
    height: auto;
  }


}


